<template>
  <div class="minesweeper">
    <h1>{{ msg }}</h1>
    Ê®™Ôºö<input type="number" step="1" min="2" name="yoko" id="yoko" v-model="yoko" />
    Á∏¶Ôºö<input type="number" step="1" min="2" name="tate" id="tate" v-model="tate" />
    ÁàÜÂºæÔºö<input type="number" step="1" min="1" v-bind:max="getMaxBomb" name="bomb" id="bomb" v-model="bomb" />
    <button @click="bombShuffle">‰ΩúÊàêÔºÅ</button>

    <div id="playground" v-if="isDisp">
      <table border="1">
        <tr v-for="(cols,y) in box" :key="y">
          <td v-for="(cell,t) in cols" :key="t" @click="isBomb(cell,y,t)" v-on:click.right.prevent="toggleFlag(cell)">
            <div v-if="cell.bombDispKbn === 1">‚úñÔ∏é</div>
            <div v-else-if="cell.bombDispKbn === 2">{{cell.bombNext}}</div>
            <div v-else-if="cell.bombDispKbn === 3">-</div>
            <div v-else-if="cell.flag">üö©</div>
          </td>
        </tr>
      </table>
      <div v-if="finish === 1">SUCCESS!</div>
      <div v-if="finish === 2">GAME OVER</div>
    </div>
  </div>
</template>

<script>
// ÁàÜÂºæÈÖçÁΩÆ
let bombShuffle = function(){
  if (this.bomb > this.getMaxBomb) {
    alert("ÁàÜÂºæÊï∞„ÅØ"+this.getMaxBomb+"„Åæ„Åß„Å´„Åó„Å¶„Åè„Å†„Åï„ÅÑ„ÄÇ");
    return;
  }
  // ÂàùÊúüÂåñ
  this.finish = 0;
  this.box.splice(-this.box.length);
  // ÁàÜÂºæ„Éï„É©„Ç∞
  for (let y=0; y<this.yoko; y++){
    this.box.splice(y,1,[]);
    for (let t=0; t<this.tate; t++){
      this.box[y].splice(t,1,{
        bomb: 0,
        bombNext: 0,
        bombDispKbn: 0,
        flag: false
      });
    }
  }
  let bombCount = this.bomb;
  while(bombCount > 0) {
    let targetY = Math.floor(Math.random() * this.yoko);
    let targetT = Math.floor(Math.random() * this.tate);

    // „Åô„Åß„Å´ÁàÜÂºæ„Åå„Çª„ÉÉ„Éà„Åï„Çå„Å¶„ÅÑ„ÇãÂ†¥Âêà„ÅØ‰ªñ„ÅÆ„Éû„Çπ„ÇíÊé¢„Åô
    if (this.box[targetY][targetT].bomb === 1){
      continue;
    }

    // ÁàÜÂºæ„Çí„Çª„ÉÉ„Éà„Åô„Çã
    this.box[targetY][targetT].bomb = 1;
        
    // ÁàÜÂºæ„ÅÆÈö£„ÅÆ„Éû„Çπ„Å´„Éï„É©„Ç∞„ÇíÁ´ã„Å¶„Çã
    for (let ny=targetY-1;ny<=targetY+1 ; ny++){
      // ÈÖçÂàó„ÇíË∂Ö„Åà„Å¶„Åó„Åæ„ÅÜÂ†¥Âêà„ÅØÊ¨°„ÅÆ„É´„Éº„Éó
      if (ny < 0 || ny >= this.yoko) {
        continue;
      }
      for (let nt=targetT-1;nt<=targetT+1 ; nt++){
        if (nt < 0 || nt >= this.tate) {
          continue;
        }
        if (ny === targetY && nt === targetT){
          continue;
        }
        if (typeof this.box[ny][nt].bombNext !== "undefined"){
          this.box[ny][nt].bombNext = this.box[ny][nt].bombNext + 1;
        }
      }
    }
    
    // ÁàÜÂºæ„Çª„ÉÉ„ÉàÊï∞„Çí1„Å§Ê∏õ„Çâ„Åô
    bombCount--;
  }
  console.log(this.box);
  this.isDisp = true;
}

// „ÇØ„É™„ÉÉ„ÇØ„Åó„ÅüÂ†¥ÊâÄ„ÅåÁàÜÂºæ„Åã„Å©„ÅÜ„ÅãÂà§ÂÆö
let isBomb = function(bombFlg,y,t){
  if (bombFlg.bombDispKbn > 0 || bombFlg.flag){
    return;
  }
  if (bombFlg.bomb === 1){
    // console.log("„Åì„Çå„ÅØÁàÜÂºæ„Åß„Åô„ÄÇ");
    alert("„ÅÇ„Å™„Åü„ÅØÊ≠ª„Å´„Åæ„Åó„Åü„ÄÇ");
    this.dispAllResult();
    this.finish = 2;
    return;
  } 

  if (bombFlg.bombNext > 0){
    //console.log("„Åì„Çå„ÅØÁàÜÂºæ„ÅÆÈö£„Å´„ÅÇ„Çä„Åæ„Åô:"+bombFlg.bombNext);
    bombFlg.bombDispKbn = 2;
  } else {
    //console.log("„Åì„Çå„ÅØÁàÜÂºæ„ÅÆËøë„Åè„Åß„ÅØ„ÅÇ„Çä„Åæ„Åõ„Çì");
    bombFlg.bombDispKbn = 3;
    // todo
    this.checkNextCell(y,t);
  }
  this.checkSuccess();
}

// „ÇØ„É™„Ç¢„Åß„Åç„Åü„Åã„Å©„ÅÜ„Åã
let checkSuccess = function(){
  let successCount = 0;
  for(let i=0;i<this.tate;i++){
    successCount = successCount + (this.box[i].filter(cell => cell.bombDispKbn === 0).length);
  }
  if (successCount === this.bomb){
    this.finish = 1;
    this.dispAllResult();
    alert("„Åä„ÇÅ„Åß„Å®„ÅÜÔºÅ„ÇØ„É™„Ç¢„Åó„Åæ„Åó„ÅüÔºÅ");
  }
}

// ÂÖ®„Éû„ÇπÁµêÊûúË°®Á§∫
let dispAllResult = function(){
  for(let y=0;y<this.box.length;y++){
    for(let t=0;t<this.box[y].length;t++){
      if (this.box[y][t].bomb === 1){
        this.box[y].splice(t,1,{
          bomb: this.box[y][t].bomb,
          bombNext: this.box[y][t].bombNext,
          bombDispKbn: 1,
          flag: false
        })
      } else if (this.box[y][t].bombNext > 0) {
        this.box[y].splice(t,1,{
          bomb: this.box[y][t].bomb,
          bombNext: this.box[y][t].bombNext,
          bombDispKbn: 2,
          flag: false
        })
      } else {
        this.box[y].splice(t,1,{
          bomb: this.box[y][t].bomb,
          bombNext: this.box[y][t].bombNext,
          bombDispKbn: 3,
          flag: false
        })
      }
    }
  }
}

// Èö£„ÅÆ„Éû„Çπ„ÉÅ„Çß„ÉÉ„ÇØ
// ‰∏ä‰∏ãÂ∑¶Âè≥„ÅÆÈö£„ÇíÂÜçÂ∏∞ÁöÑ„Å´Á¢∫Ë™ç„Åó„Å¶„ÅÑ„Åè
// y:yoko 0 <= y <= this.yoko
// t:tate 0 <= t <= this.tate
let checkNextCell = function(y, t){
  // ÁàÜÂºæ„ÅÆÈö£„ÅÆ„Éû„Çπ„Å´„Éï„É©„Ç∞„ÇíÁ´ã„Å¶„Çã
  for (let i=y-1;i<=y+1;i++){
    // ÈÖçÂàó„ÇíË∂Ö„Åà„Å¶„Åó„Åæ„ÅÜÂ†¥Âêà„ÅØÊ¨°„ÅÆ„É´„Éº„Éó
    if (i < 0 || i >= this.yoko) {
      continue;
    }
    for (let j=t-1;j<=t+1;j++){
      // ÈÖçÂàó„ÇíË∂Ö„Åà„Å¶„Åó„Åæ„ÅÜÂ†¥Âêà„ÅØÊ¨°„ÅÆ„É´„Éº„Éó
      if (j < 0 || j >= this.tate) {
        continue;
      }
      // Ëá™ÂàÜËá™Ë∫´„ÅØ„ÉÅ„Çß„ÉÉ„ÇØÁµÇ„Çè„Å£„Å¶„ÅÑ„Çã„Åü„ÇÅ„ÄÅÈô§Â§ñ
      if (i === y && j === t){
        continue;
      }
      // Ë°®Á§∫„ÅåÁµÇ„Çè„Å£„Å¶„ÅÑ„ÇãÁÆáÊâÄ„ÅØÂØæË±°Â§ñ
      if (this.box[i][j].bombDispKbn > 0){
        continue;
      }
      // „ÉÅ„Çß„ÉÉ„ÇØÂá¶ÁêÜ
      if (this.box[i][j].bombNext > 0) {
        this.box[i][j].bombDispKbn = 2;
      } else {
        this.box[i][j].bombDispKbn = 3;
        this.checkNextCell(i,j);
      }
    }
  }
}

// Êóó„Çí„Åü„Å¶„Çã„ÄÅ‰∏ã„Çç„Åô
let toggleFlag = function(bombFlg){
  if (bombFlg.bombDispKbn > 0){
    return;
  }
  bombFlg.flag = !bombFlg.flag;
}

export default {
  name: 'Minesweeper',
  props: {
    msg: String
  },
  computed: {
    getMaxBomb: function(){
      return this.tate * this.yoko
    }
  },
  data() {
    return {
      area: {
        tate: 9,
        yoko: 9,
        bomb: 10,
        finish: 0
      },
      isDisp: false,
      box: [] //[[{bomb:0,bombNext:1,bombDispKbn:0},{bomb:1,bombNext:2,bombDispKbn:1}...]]
    }
  },
  methods: {
    bombShuffle: bombShuffle,
    isBomb: isBomb,
    dispAllResult: dispAllResult,
    checkNextCell: checkNextCell,
    toggleFlag: toggleFlag,
    checkSuccess: checkSuccess
  }
}
</script>

